.features__list {
  @include list;
}

@media (min-width: $tablet-width) {
  .features {
    position: relative;
    margin-top: 150px;
    padding-top: 1px;
  }
}

@media (min-width: $desktop-width) {
  .features {
    position: relative;
    margin-top: 200px;
    background: url("../img/decor/gear.png") no-repeat 0 1175px;

    &::before {
      content: "";
      position: absolute;
      top: -100px;
      right: 190px;
      width: 292px;
      height: 768px;
      background: url("../img/decor/rizor-decor.png") no-repeat center;
    }
  }

  .features__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

