.main-nav {
  @include list;
  text-align: center;
}

.main-nav__item {
  border-bottom: 1px solid #000000;
}

.main-nav__link {
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  display: inline-block;
  width: 100%;
  padding: 20px 0;
  color: #000000;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .main-nav {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
  }

  .main-nav__link {
    letter-spacing: 1px;
  }

  .main-nav__item {
    border: none;
  }
}

@media (min-width: $desktop-width) {
  .main-nav__link {
    font-size: 14px;
  }
}
