.for {
  margin-top: 134px;
  color: #ffffff;
  padding-bottom: 80px;
  background-color: #3a2ff8;
}

.for__container {
  display: flex;
  flex-direction: column;
}

.for__image {
  display: block;
  margin: -63px auto 0;
  box-shadow: 0 8px 14px 0 rgba(0, 1, 1, 0.25);
}

.for__info {
  display: flex;
  flex-direction: column;
}

.for__title {
  @include font(14px, 30px, bold);
  text-align: center;
  margin-top: 42px;
}

.for__description {
  margin-top: 24px;
}

.for__text {
  margin: 0;

  &:nth-child(2) {
    margin-top: 11px;
  }
}

.for__button {
  @include font(14px, 30px, bold);
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #3a30f8;
  margin: 70px auto 0;
  padding: 10px 57px;
  background-color: #ffffff;
  border-radius: 50px;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 1, 0.2);

  &:hover,
  &:focus {
    color: rgba(#3a30f8, 0.6);
  }

  &:active {
    color: rgba(#3a30f8, 0.3);
  }
}

@media (min-width: $tablet-width) {
  .for {
    margin-top: 205px;
  }

  .for__image {
    width: 724px;
    height: 461px;
    margin-top: -61px;
  }

  .for__title {
    text-align: left;
    letter-spacing: 1px;
    margin-top: 51px;
    margin-left: $tablet-margin-left;

    @media (min-width: 800px) {
      width: 628px;
      margin-right: auto;
      margin-left: auto;
    }
  }

  .for__description {
    display: flex;
    margin-top: 32px;
    margin-left: $tablet-margin-left;
    margin-right: $tablet-margin-left - 20px;

    @media (min-width: 800px) {
      margin-right: auto;
      margin-left: auto;
    }
  }

  .for__text {
    display: inline-block;

    &:first-child {
      width: 316px;
      margin-right: 42px;
    }

    &:nth-child(2) {
      margin-top: 0;
      width: 270px;
    }
  }

  .for__button {
    margin-top: 46px;
  }
}

@media (min-width: $desktop-width) {
  .for {
    position: relative;
    margin-top: 297px;
    padding-bottom: 86px;

    &::before {
      content: "";
      position: absolute;
      z-index: 0;
      top: -16px;
      left: 0;
      width: 511px;
      height: 790px;
      background: url("../img/decor/gear.png") no-repeat 0 0;
      opacity: 0.1;
    }
  }

  .for__container {
    flex-direction: row-reverse;
    margin-left: $desktop-margin-left;
  }

  .for__image {
    width: 1176px;
    height: 750px;
    margin-left:auto;
  }

  .for__info {
    position: relative;
    z-index: 1;
    margin-right: 10px;
  }

  .for__title {
    width: auto;
    font-size: 20px;
    margin-top: 92px;
    margin-left: 0;
  }

  .for__description {
    flex-direction: column;
    margin-top: 75px;
    margin-left: 0;
    margin-right: 0;
  }

  .for__text {
    width: auto;

    &:nth-child(2) {
      margin-top: 20px;
    }
  }

  .for__button {
    margin-top: 94px;
    margin-left: 0;
  }
}
