.title {
  @include font(24px, 24px);
  font-family: "RubikMonoOne";
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0;

  &--section {
    @include font(14px, 80px, bold);
    font-family: "Roboto";
    color: #3a30f8;
    text-align: center;
    letter-spacing: 1px;
    margin-top: 37px;
  }
}

@media (min-width: $tablet-width) {
  .title {
    &--section{
      position: absolute;
      margin-top: 68px;
      margin-left: 423px;
      text-align: left;
    }
  }
}

@media (min-width: $desktop-width) {
  .title {
    font-size:40px;
    line-height: 40px;

    &--section{
      font-size: 20px;
      margin-top: 85px;
      margin-left: 845px;
      letter-spacing: 2px;
    }
  }
}
