.indicators__text {
  margin-top: 10px;
}

.indicators__play {
  display: none;
}

.indicators__image {
  margin-top: 36px;
}

@media (min-width: $tablet-width) {
  .indicators {
    margin-left: $tablet-margin-left + 20px;
    margin-right: $tablet-margin-left - 20px;
  }

  .indicators__text {
    margin-top: 12px;
  }

  .indicators__video {
    width: 538px;
    height: 320px;
    margin-right: auto;
    margin-left: auto;
  }

  .indicators__image {
    width: 538px;
    height: 320px;
    margin-top: 42px;
  }

  .indicators__play {
    display: inline-block;
    top: 214px;
  }
}

@media (min-width: $desktop-width) {
  .indicators {
    width: 100%;
    margin-top: -200px;
    margin-left: $desktop-margin-left + $desktop-padding;
    margin-right: $desktop-margin-left + $desktop-padding;
  }

  .indicators__description {
    position: relative;
    width: 470px;
    margin-left: $desktop-padding;

    &::before {
    content: "";
    position: absolute;
    top: 18px;
    left: -78px;
    width: 37px;
    height: 362px;
    background: url("../img/decor/play-decor-i.png") no-repeat center;
    }
  }

  .indicators__video {
    width: 830px;
    height: 400px;
    margin: 0 70px;
  }

  .indicators__image {
    width: 830px;
    height: 400px;
  }

  .indicators__play {
    top: 145px;
    left: 10px;
  }
}
