@mixin font($size: 16px, $line-height: 24px, $weight: 400) {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: $weight;
  font-size: $size;
  line-height: $line-height;
}

@mixin list {
  list-style: none;
  padding: 0;
  margin: 0;
}

@mixin visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  border: 0;

  clip: rect(0 0 0 0);
}

@mixin list-item {
  position: relative;
  text-transform: lowercase;
  margin-top: 16px;
  padding-left: 38px;

  &:first-child {
    margin-top: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 2px;
    width: 30px;
    height: 5px;
    background: url("../img/decor/li.jpg") no-repeat center;
  }
}
