.properties {
  position: relative;
  display: flex;
  margin-top: 45px;
  margin-left: $mobile-margin-left;
}

.properties__item {
  display: flex;
  flex-direction: column-reverse;

  &:first-child {
    margin-right: 42px;
  }

  &:nth-child(2) {
    align-self: flex-start;
  }

  &--electronics {
    position: relative;
    padding-top: 52px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
    }

    &:first-child {
      margin-right: 10px;
    }
  }

  &--engine::before {
    width: 54px;
    height: 42px;
    background: url("../img/decor/engine-mobile.png") no-repeat center;

    @media (min-width: $desktop-width) {
      width: 76px;
      height: 60px;
      background-image: url("../img/decor/engine-desktop.png");
    }
  }

  &--gyroscope::before {
    top: 20px;
    width: 69px;
    height: 22px;
    background: url("../img/decor/gyroscope-mobile.png") no-repeat center;

    @media (min-width: $desktop-width) {
      top: 12px;
      width: 98px;
      height: 32px;
      background-image: url("../img/decor/gyroscope-desktop.png");
    }
  }
}

.properties__item--accum {
  &:first-child {
    margin-right: 57px;
  }
}

.properties__title {
  font-size: 14px;
  line-height: 18px;
  color: #666666;
  margin: 0;
  margin-top: 8px;

  &--accum {
    margin-left: -7px;
  }
}

.properties__text {
  font-size: 40px;
  line-height: 30px;
  color: #3a30f8;
  width: 120px;
  margin: 0;
  margin-left: -6px;
  letter-spacing: -3px;

  &--low {
    font-size: 18px;
    letter-spacing: -1px;
  }

  &--modes {
    margin-bottom: 6px;
  }
}

@media (min-width: $tablet-width) {
  .properties {
    margin-left: 0;
  }

  .properties__item {
    &--electronics:nth-child(2) {
      margin-top: 16px;
    }
  }

  .properties__title {
    &--accum {
      width: 100px;
    }
  }
}

@media (min-width: $desktop-width) {
  .properties {
    margin-top: 74px;
  }

  .properties__item {

    &:first-child {
      margin-right: 76px;
    }

    &--electronics {
      &:first-child {
        margin-right: 70px;
      }

      & .properties__title {
        margin-top: 16px;
      }
    }
  }

  .properties__item--accum {
    &:first-child {
      margin-right: 50px;
    }
  }

  .properties__title {
    font-size: 16px;

    &--accum {
      width: 120px;
      margin-left: 0;
    }
  }

  .properties__text {
    font-size: 60px;
    line-height: 60px;

    &--low {
      font-size: 23px;
    }
  }
}
