.electronics {
  padding-bottom: 0;
}

.electronics__description {
  margin-left: 10px;
}

.electronics__text {
  &:nth-child(2) {
    margin-top: 12px;
  }
}

.electronics__properties {
  margin-top: 33px;
  margin-left: $mobile-margin-left + 6px;
}

.electronics__video {
  margin-top: 43px;
  margin-left: -$mobile-padding;
  @media (min-width: 321px) {
    margin-left: auto;
  }
}

@media (min-width: $tablet-width) {
  .electronics {
    display: flex;
    flex-direction: column-reverse;
    margin: 0 $tablet-margin-left;
  }

  .electronics__container {
    margin-top: 27px;
  }

  .electronics__description {
    display: flex;
  }

  .electronics__text {
    margin-left: -$mobile-padding;
  }

  .electronics__properties {
    display: flex;
    flex-direction: column;
    margin-top: 0;
    margin-left: 120px;
    margin-right: 97px;
  }

  .electronics__video {
    width: 578px;
    height: 277px;
    margin-top: 45px;
    margin-left: auto;
    margin-right: auto;
  }

  .electronics__image {
    width: 578px;
    height: 277px;
  }
}

@media (min-width: $desktop-width) {
  .electronics {
    flex-direction: column;
    width: 430px;
  }

  .electronics__container {
    margin-top: 0;
  }

  .electronics__title {
    margin-top: 90px;
  }

  .electronics__description {
    flex-direction: column;
  }

  .electronics__properties {
    flex-direction: row;
    margin-top: 38px;
    margin-left: -11px;
  }

  .electronics__video {
    width: 400px;
    height: 280px;
    margin-top: 62px;
  }

  .electronics__image {
    width: 400px;
    height: 280px;
  }
}
