.side-nav {
  display: none;
}

.side-nav__list {
  @include list;
  position: relative;
  padding: 327px 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    width: 1px;
    height: 327px;
    background-color: #392ff7;
    transform: translateX(-50%);
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: -3px;
  }
}

.side-nav__item {
  display: flex;
  justify-content: center;
  width: 17px;
  height: 30px;
  text-align: center;
}

.side-nav__link {
  display: inline-block;
  align-self: center;
  width: 7px;
  height: 7px;
  border-radius: 3px;
  background-color: #392ff7;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: lighten(#392ff7, 10%);
  }

  &:active {
    opacity: 0.8;
  }

  &--active {
    width: 13px;
    height: 13px;
    border-radius: 7px;
    background-color: transparent;
    border: 2px solid #3a30f8;

      &:hover,
      &:focus {
        background-color: transparent;
        border-color: lighten(#3a30f8, 10%);
      }
  }
}

@media (min-width: $desktop-width) {
  .side-nav {
    display: block;
    position: fixed;
    z-index: 10;
    top: 50%;
    right: $desktop-padding;
    transform: translateY(-50%);
  }
}
