.video {
  width: 320px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.video__button {
  position: absolute;
  display: inline-block;
  top: 49%;
  left: 51%;
  width: 25px;
  height: 28px;
  padding: 0;
  background: transparent;
  border: none;
  transform: translate(-50%, -50%);
  cursor: pointer;

  &:hover .video__play-image:not(.video__play-image-circle),
  &:focus .video__play-image:not(.video__play-image-circle) {
    transform: translate(-50%, -50%) scale(1.1);
  }

  &:active .video__play-image:not(.video__play-image-circle) {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0.3;
  }

  &--circle {
    width: 60px;
    height: 60px;
    background: rgba(#ffffff, 0.8);
    border-radius: 50%;

    &:hover,
    &:focus {
      transform: translate(-50%, -50%) scale(1.1);
    }

    &:active {
      transform: translate(-50%, -50%) scale(0.8);
      opacity: 0.3;
    }
  }
}

.video__play-image {
  position: absolute;
  width: 25px;
  height: 28px;
  top: 50%;
  left: 56%;
  transform: translate(-50%, -50%);
}

@media (min-width: $tablet-width) {
  .video {
    width: auto;
    margin-left: 0;
    margin-right: 0;
  }

  .video__button {
    width: 40px;
    height: 45px;

    &--out {
      top: 119px;
      left: 8px;

      &::before {
        content:"Смотреть видео";
        position: absolute;
        top: 11px;
        left: -43px;
        width: 100%;
        text-align: left;
        @include font(10px, 14px, bold);
        letter-spacing: 1px;
        text-transform: uppercase;
        transform: rotate(-90deg);
      }
    }

    &--circle {
      width: 100px;
      height: 100px;
    }
}

  .video__play-image {
    width: 40px;
    height: 45px;
  }
}

@media (min-width: $desktop-width) {
  .video__button--out {
    top: 111px;
    left: -2px;
    width: 72px;
    height: 70px;

    & .video__play-image {
      width: 72px;
      height: 70px;
    }

    &::before {
      top: 16px;
      left: -50px;
      font-size: 12px;
    }
  }
}
