.feature {
  padding-bottom: 48px;
}

.feature__title {
  text-align: center;
  margin-top: 24px;
}

.feature__text {
  margin: 0;
  margin-top: 30px;
  margin-left: $mobile-margin-left;

  &--indicators,
  &--accum,
  &--electronics {
    margin-left: 0;
  }

  &--mode {
    @include list-item;
    text-transform: none;
  }
}

.feature__image {
  margin-left: -$mobile-padding;
  margin-top: 38px;
}

.feature__list {
  @include list;
  margin-top: 23px;
  margin-left: $mobile-margin-left;
}

.feature__list-item {
  @include list-item;
}

@media (min-width: $tablet-width) {
  .feature {
    &__design {
      display: flex;
    }
  }

  .feature__title {
    text-align: left;
    letter-spacing: 3px;
  }

  .feature__text {
    margin-left: 0;
  }

  .feature__image {
    margin-left: 0;
  }

  .feature__list {
    margin-left: 0;
  }
}

@media (min-width: $desktop-width) {
  .feature__text {
    margin-top: 26px;

    &--mode {
      padding-left: 44px;
    }
  }

  .feature__list {
    margin-top: 20px;
  }

  .feature__list-item {
    margin-top: 20px;
    padding-left: 42px;
}
}
